import React, { Component } from "react";
import styled from 'styled-components';
import { BREAKPOINT_LG,BREAKPOINT_MDS,FONT_FAMILY_HEADER,MAX_WIDTH, BREAKPOINT_MDL,MAIN_HEADER_COLOR,HEADER_COLOR,NAV_LIST_COLOR,FONT_COLOR, BREAKPOINT_MD, BREAKPOINT_SM, HEADER_BACKGROUND, MAIN_HEADER_SUB_COLOR, BLUE_COLOR} from '../types';



const StyledHero = styled.div`
.hero-sticky{    
  background:${HEADER_BACKGROUND};
  .hero{ max-width:${MAX_WIDTH};
    text-align: left;


    position: relative;
    margin: 0 auto;
    display:flex;
    margin-top:80px;
    padding-top: 0px;
    padding-bottom: 0px;
 

    transform:${props => props.is_Mounted ?    'translateY(0px)' : 'translateY(50px)'};
     opacity:${props => props.is_Mounted ?    '1' : '0'};
     transition:0.5s ease;
     @media screen and (max-width: ${BREAKPOINT_LG}) {
      display:block;
      padding-top: 80px;
     }
     @media screen and (max-width: ${BREAKPOINT_MDL}) {
      max-width: 600px;
      padding-top: 60px;
      margin-left: 0px;
     }
     
    .hero-side{padding-right: 25px;
        @media screen and (max-width: ${BREAKPOINT_LG}) {
      
  padding: 0px 30px;
  p{
    display:none;
  }

        }
      flex:1;
      p {
        color: ${MAIN_HEADER_SUB_COLOR};
        font-size: 30px;
        margin: 0 auto;
        font-weight: 300;
        text-align: left;
       height:170px;
        max-width: 550px;
        line-height: 1.4;
        @media screen and (max-width: ${BREAKPOINT_LG}) {
          font-size: 18px;
          text-align: left;
          margin-left: 0px;
        }
        @media screen and (max-width: ${BREAKPOINT_MD}) {
          font-size: 16px;
        }
        @media screen and (max-width: ${BREAKPOINT_SM}) {
          font-size: 15px;
        }
      }
    }
    .hero-text{
     flex:2;
    
     @media screen and (max-width: ${BREAKPOINT_LG}) {
        padding-right: 20px;
        padding-left: 20px;
        }

     .hero-header{    
         
      text-align:center;
        @media screen and (max-width: ${BREAKPOINT_LG}) {
      
    height: auto!important;
    margin-bottom: 25px;

        }
        p {
          
          font-size: 19px;
    margin: 0 auto;
    margin-top: 15px;
    margin-bottom: 5px;

    max-width: 600px;     color: ${MAIN_HEADER_SUB_COLOR};
    @media screen and (max-width: ${BREAKPOINT_MD}) {
          font-size: 16px;
        }
}
      h2 {
        font-size: 48px;
        margin-top: 0px;
        font-family:${FONT_FAMILY_HEADER};
        color: ${MAIN_HEADER_COLOR} ;
    
        line-height: 1.13;
        letter-spacing: -.5px;
        font-weight: 700 !important;
        text-rendering: optimizeLegibility;
        position: relative;
        word-break: break-word;
        display: inline;
        background-size: 100% 100%;
        @media screen and (max-width: 768px) {
          font-size: 32px;
          line-height: 1.25;
          text-align: left;
        }
       
        

        }}
     
       text-align: left;
    
      .hero-head{
        max-width:690px;
    text-align: left;
    margin:0 auto;
        padding: 70px 10px; 
        padding-bottom: 30px;
        @media screen and (max-width: ${BREAKPOINT_LG}) {
          text-align: left;
          padding: 0 10px!important;
        }

    }
  }
  }
  
}

`;
class HeroSection extends Component {
    constructor(props) {
        super(props);
        this.state = {
          is_Mounted:true,
      };
    }
      componentDidMount(){
        setTimeout(
          function() {
            this.setState({is_Mounted:true})
      
          }
          .bind(this),
          10
      );
         
      }
    

    render() {
        
        return (
            <StyledHero is_Mounted={this.state.is_Mounted}>
        <div className="hero-sticky">
          <div className="hero">
            <div className="hero-text"  >
              <div className="hero-head" >

             <div className="hero-header" > <h2>High Quality Web Icons Set </h2>
            <p>Boxicons is a simple vector icons set carefully crafted for designers and developers to use in your next project</p>
            
            
            </div>
          

</div>
             
                </div>
               
            </div>
          </div>
         
      </StyledHero>
        );
    }
}
export default HeroSection;